.home-dark {
  background-color: #111827;
  color: antiquewhite;
}
.home-dark .icon {
  color: antiquewhite;
}
.home-light {
  background-color: aliceblue;
  color: #111827;
}


.text-column {
  display: flex;
  align-items: center;
}

.text-content h1{
  opacity: 0;
  -webkit-animation: slideBottom 1s ease forwards;
          animation: slideBottom 1s ease forwards;
  -webkit-animation-delay: .7s;
          animation-delay: .7s;
  text-align: center;
}
.text-content h2{
  opacity: 0;
  -webkit-animation: slideBottom 1s ease forwards;
          animation: slideBottom 1s ease forwards;
  -webkit-animation-delay: .7s;
          animation-delay: .7s;
  text-align: center;
}
.home-dark .text-content h2 span{
  color: cyan;
}
.home-light .text-content h2 span{
  color: #3a0ca3;
}
.text-content p{
  font-size: larger;
  margin-left: 15px;
  opacity: 0;
  -webkit-animation: slideLeft 1s ease forwards;
          animation: slideLeft 1s ease forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.text-content {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.home-dark .icons a{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background: transparent;
  border: 2px solid cyan;
  border-radius: 50%;
  font-size: 20px;
  color:cyan;
  margin: 10px 10px 10px 0px;
  transition: .5s ease;
  opacity: 0;
  -webkit-animation: slideRight 1s ease forwards;
          animation: slideRight 1s ease forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  
}
.home-light .icons a{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  color: #111827;
  background: transparent;
  border: 2px solid #3a0ca3;
  border-radius: 50%;
  font-size: 20px;
  margin: 10px 10px 10px 0px;
  transition: .5s ease;
  opacity: 0;
  -webkit-animation: slideRight 1s ease forwards;
          animation: slideRight 1s ease forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  
}

.home-dark .icons a:hover{
  background-color: cyan;
  color: #111827;
  box-shadow: 0 0 5px cyan;
}
.home-light .icons a:hover{
  background-color: #3a0ca3;
  color: white;
  box-shadow: 0 0 5px #3a0ca3;
}

.icon {
  margin-top: 10px;
  margin-bottom: 10px;
}
.icon:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.home-dark .resume-btn:hover{
  box-shadow: 0 0 20px cyan;
}
.home-light .resume-btn:hover{
  color: white !important;
